@use 'base/variables' as v;
@use 'base/mixins' as m;


@media (prefers-color-scheme: dark) {
    $grisOscuro: #2c2c3b;

    body {
        background-color: $grisOscuro;
    }
    .tagline,
    .formulario label,
    .login .acciones a,
    .crear .acciones a,
    .olvide .acciones a   {
        color: v.$blanco;
    }

    .descripcion-pagina,
    .formulario .campo input,
    .dashboard .contenido .nombre-pagina {
        color: darken(v.$grisClaro, 15%);
    }

    .formulario .campo input {
        background-color: lighten($grisOscuro, 15%);

        &::placeholder {
            color: darken(v.$grisClaro, 30%);
        }
    }


    .alerta.error {
        background-color: rgba(255, 70, 70, 0.397);
        color: v.$blanco;
    }
    
    // **** SIDEBAR **** //
    .sidebar {
        @include m.tablet {
            background-color: darken(v.$grisOscuro, 5%);
        }

        .sidebar-nav a {
            &.activo,
            &:hover {
                background-color: v.$indigo;
            }
        }
    }

    // **** BARRA **** //
    .barra-mobile,
    .barra {
        background-color: lighten($grisOscuro, 7%);
    }
    .barra {
        p {
            color:  v.$azul;
            
            span {
                color: v.$grisClaro;
            }
        }
        .cerrar-sesion {
            @include m.boton(v.$indigo, v.$blanco);
            padding: 1rem;
        }
        
    }

    // **** DASHBOARD **** //

    .filtros {
        background-color: lighten($grisOscuro, 10%);
        .filtros-inputs h2, label {
            color: v.$blanco;
        
        }
     
        input[type="radio"] {
            accent-color: $grisOscuro;
        }
    }

  
    .dashboard {
        background-color: $grisOscuro;

        .enlace {
            color: darken(v.$grisClaro, 15%);

            &:hover {
                color: darken(v.$grisClaro, 20%);
            }
        }
        .formulario input:not([type=submit]) {
            background-color: lighten($grisOscuro, 20%);
            color: v.$blanco;

            &::placeholder {
                color: darken(v.$blanco, 25%);
            }
        }
    }

    // **** PROYECTOS **** //
    .listado-proyectos .proyecto {
        background-color: lighten(v.$grisOscuro, 15%);

        &:hover {
            background-color: lighten(v.$grisOscuro, 10%);
        }
    }

    .listado-tareas .tarea {
        background-color: lighten($grisOscuro, 10%);
        border: none;
        p {
            color: v.$grisClaro;
        }
    }

    .modal .formulario {
        background-color: $grisOscuro;
        input[type="text"] {
            background-color: lighten($grisOscuro, 20%);
            border: none;
            &::placeholder {
                color: darken(v.$blanco, 15%);
            }
        }
        legend {
            color: v.$grisClaro;
        }
    }
}